.article-card {
  margin-bottom: 30px;
}

.article-card .a-1 {
  text-decoration: none;
  font-size: 18px;
  color: #404343;
  font-style: italic;
  margin: 0;
}

.article-card .a-1:hover {
  color: var(--theme-secondary);
}

.article-card hr {
  width: 100px;
  margin-top: 4px;
  color: var(--theme-secondary);
  height: 3px !important;
}

.article-card img {
  width: 100%;
}

.article-card .a-2 {
  text-decoration: none;
  font-style: italic;
  margin: 0;
}
